<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
  export default {
    data () {
      return {
        flag: true
      }
    },
    mounted () {
      this.IsPC()
    },
    methods: {
      IsPC () {
        const that = this
        const userAgentInfo = navigator.userAgent
        const Agents = ['Android', 'iPhone',
          'SymbianOS', 'Windows Phone',
          'iPad', 'iPod']
        for (var v = 0; v < Agents.length; v++) {
          if (userAgentInfo.indexOf(Agents[v]) > 0) {
            that.flag = false
//            this.$router.push('/move')
            break
          } else {
            that.flag = true
          }
        }
      }
    }
  }
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
}
*{
  margin: 0;
  padding: 0;
  outline: none;
}
html,body{
  width: 100%;
  background-color: #f0f0f0;
}

img {
  border: none;
  text-decoration: none;
}
a{
  text-decoration: none;
  color: #333;
}
input{
  outline: none;
  border: none;
  font-size: 16px;
}
input:focus{
  font-size: 16px;
}
ul,li{
  list-style-type: none
}
el-input{
  outline: none;
  font-size: 30px;
}
el-input:focus{
  font-size: 30px;
}
</style>



