import axios from 'axios'
import {baseUrl} from './base'
/**
 *url上加参数  支持中文
 * @param url
 * @param obj
 * @returns {string}
 */
function getUrlJoinParams(url, obj) {
  var result = url + '?';
  for (var p in obj) {
    if (typeof ( obj[p]) == 'function') {
      continue;
    } else {
      result += encodeURI(p) + '=' + encodeURI(obj[p]) + '&';
    }
  }
  result = result.substring(0, result.length - 1);
  return result;
};

/**
 * {a:1,c:2,b:3} => a=1&c=2&b=3
 * 对象转string objectToString(obj,[separator]) ;//separator默认是& 分隔符
 * @param obj
 * @returns {string}
 */
function objectToString(obj) {
  var separator = arguments[1] || '&';
  var result = '';
  for (var p in obj) {
    if (typeof ( obj[p]) == 'function') {
      continue;
    } else {
      result += p + '=' + obj[p] + separator;
    }
  }
  result = result.substring(0, result.length - 1);
  return result;
};

/**
 *  a=1&c=2&b=3 =>{a:1,c:2,b:3}
 * @param str
 * @returns {{}}
 */
function stringToObject(str) {
  var paramters = {};
  var separator = arguments[1] || '&';
  var paramterStr = str.split(separator); //字符分割

  paramterStr.forEach(function (item, index) {
    var obj = item.split('=');
    if (obj.length == 2) {
      paramters[obj[0]] = obj[1];
    }
  });
  return paramters;
};

/**
 * httpGet
 */
function httpGet(url,parameters,callback) {
  var that = this;
  url = getUrlJoinParams(url, parameters);
  axios.get(baseUrl + url).then(function (res) {
    if(res.data.error_code == -100){
      console.log('请登录')
      return
    }
    callback(res)
  })
}
/**
 * httpPost
 */
function httpPost(url,parameters,callback){
  var that = this;
  url = getUrlJoinParams(url, parameters);
  axios.post(baseUrl + url).then(function (res) {
    // if(res.data.error_code == -100){
    //   console.log('请登录')
    //   alert('请登录')
    //   return
    // }
    callback(res)
  })
}
/**
 * 存储localStorage
 */
function setStore(name, content){
  if (!name) return;
  if (typeof content !== 'string') {
    content = JSON.stringify(content);
  }
  window.localStorage.setItem(name, content);
}

/**
 * 获取localStorage
 */
function getStore(name){
  if (!name) return;
  return window.localStorage.getItem(name);
}

/**
 * 删除localStorage
 */
function removeStore(name){
  if (!name) return;
  window.localStorage.removeItem(name);
}
/**
 * 判断为空
 */
function isNull(p) {
  if (!p || p === undefined || p === '' || p === [] || p === {} || p === 0 || p === NaN ) {
    return true;
  }
  return false;
};
export {
  getUrlJoinParams,
  objectToString,
  stringToObject,
  httpGet,
  httpPost,
  setStore,
  getStore,
  removeStore,
  isNull
}



