<template>
  <div class="home" style="height: 100%;min-height: 1600px;">
      <div class="search" v-if="!toggle">
        <!--<input type="text" :placeholder="placeholder" v-model="content"  @keyup.enter="blacksSearch" @focus="focus" @blur="blur">-->
        <el-input :placeholder="placeholder"
                  v-model="content"
                  style="outline: none;font-size:30px;background: rgba(0,0,0,0)"
                  @keyup.enter="blacksSearch"
                  @focus="focus" @blur="blur"
                  resize="none">
        </el-input>
        <p @click="blacksSearch" style="cursor:pointer">查询</p>
      </div>
      <div style="display: flex;justify-content: flex-end;width: 100%;position: fixed;right: 40px;bottom:200px;" v-if="!toggle">
        <p @click="magnify0()"
           style="width: 100px;height: 100px;margin-right: 20px;border-radius: 10px;background-color: #fff;font-size:34px;color: #222">我要举报</p>
      </div>
      <div v-if="showNull">
        <p style="font-size:30px;color: #fff;margin-top: 20px;">请输入搜索内容</p>
      </div>
      <div v-if="!show && !toggle" style="height: 300px;"></div>
      <div class="null container" v-if="blacks.length===0 && show && !toggle">
        <img src="../../../static/null.png" alt="">
        <span>未查询到信息～</span>
      </div>
      <div class="details container" v-if="blacks.length>0 && show && !toggle">
        <div v-for="black in blacks" style="display: flex;flex-direction: column;border-bottom: 1px solid #00488B;width: 100%;padding-bottom: 20px;">
          <div class="item" v-if="black.wx_no || black.qq_no">
            <h3 v-if="black.wx_no">微信号：</h3>
            <p v-if="black.wx_no">{{black.wx_no}}</p>
            <h3 v-if="black.qq_no">QQ号：</h3>
            <p v-if="black.qq_no">{{black.qq_no}}</p>
          </div>
          <div class="item" v-if="black.mobile || black.name">
            <h3 v-if="black.mobile">手机号：</h3>
            <p v-if="black.mobile">{{black.mobile}}</p>
            <h3 v-if="black.name">姓名：</h3>
            <p v-if="black.name">{{black.name}}</p>
          </div>
          <div class="item" v-if="black.id_no || black.symbol_name">
            <h3 v-if="black.id_no">证件号：</h3>
            <p v-if="black.id_no">{{black.id_no}}</p>
            <h3 v-if="black.symbol_name">被骗币种：</h3>
            <p v-if="black.symbol_name">{{black.symbol_name}}</p>
          </div>
          <div class="item" v-if="black.wallet_address">
            <h3>骗子钱包地址：</h3>
            <p>{{black.wallet_address}}</p>
          </div>
          <div class="item" v-if="black.black_content" style="align-items: flex-start">
            <h3>举报内容：</h3>
            <p style="width: 460px;">{{black.black_content}}</p>
          </div>
          <div class="item" v-if="black.id_card_images.length>0" style="align-items: flex-start">
            <h3>证件信息：</h3>
            <div>
              <img :src="item" alt=""
                   v-for="(item,index) in black.id_card_images"
                   v-if="index % 2 !=0"
                   @click="magnify(black.id_card_images[index-1])"
                   style="width: 100px;height: 100px;cursor:pointer">
            </div>
          </div>
          <div class="item" v-if="black.images.length>0" style="align-items: flex-start">
            <h3>举报截图：</h3>
            <div>
              <img :src="item" alt=""
                   v-if="index % 2 !=0"
                   v-for="(item,index) in black.images"
                   @click="magnify(black.images[index-1])"
                   style="width: 100px;height: 100px;cursor:pointer">
            </div>
          </div>
        </div>
      </div>
    <div class="shadow" v-if="shadow_img && toggle" @click="clear">
      <img :src="shadow_img" alt="">
      <p style="font-size:50px;color: #fff;margin-top: 100px;">x</p>
    </div>
    <div class="shadow" v-if="show_code && toggle" @click="clear">
      <img src="../../../static/code.png" alt="">
      <p style="font-size:50px;color: #fff;margin-top: 100px;">x</p>
    </div>
    </div>
</template>

<script>
  import { httpGet } from '../../utils/index'
  export default {
    data () {
      return {
        input: '',
        content: '',
        blacks: [],
        showBlack: false,
        show: false,
        shadow_img: '',
        show_code: false,
        toggle: false,
        showNull: false,
        placeholder: '请输入要查询的手机号/QQ号/微信号/身份证号/姓名'
      }
    },
    methods: {
      blacksSearch () {
        const that = this
        if (!that.content) {
          that.showNull = true
          that.blacks = []
          that.show = false
          return
        }
        httpGet('/blacks/search', {content: that.content}, res => {
          that.show = true
          that.showNull = false
          if (res.data.error_code === 0) {
            that.blacks = res.data.blacks
            that.content = ''
          }
        })
      },
      magnify (img) {
        this.shadow_img = img
        this.toggle = true
      },
      magnify0 () {
        this.show_code = true
        this.toggle = true
      },
      clear () {
        this.shadow_img = ''
        this.show_code = false
        this.toggle = false
      },
      focus () {
        this.placeholder = ''
      },
      blur () {
        this.placeholder = '请输入要查询的手机号/QQ号/微信号/身份证号/姓名'
      }
    }
  }
</script>

<style scoped>
  .home{
    width: 980px;
    margin: 0 auto;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    padding-bottom: 50px;
    padding-top: 100px;
  }
  .search{
    width: 528px;
    height: 80px;
    background: #FFFFFF;
    border: 1px solid #979797;
    border-radius: 8px;
    display: flex;
    align-items: center;
    overflow: hidden;
    margin-top: 105px;
  }
  .search input{
    width: 448px;
    height: 80px;
    color: #999999;
    padding-left: 20px;
  }
  .search p{
    width: 120px;
    height: 80px;
    background: #00488B;
    line-height: 80px;
    text-align: center;
    font-size: 24px;
    color: #FFFFFF;
  }
  .null{
    background-color: #fff;
    margin-top: 50px;
    /*padding-bottom: 65px;*/
    height: 530px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 18px;
    overflow: hidden;
  }
  .null img{
    width:162px;
    height: 130px;
  }
  .null span{
    font-size: 16px;
    color: #999999;
    margin-top: 20px;
  }
  .null p{
    width: 233px;
    height: 50px;
    background: #00488B;
    border-radius: 4px;
    line-height: 50px;
    text-align: center;
    font-size: 16px;
    color: #FFFFFF;
    margin-top: 40px;
  }
  .details{
    background-color: #fff;
    margin-top: 50px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    border-radius: 20px;
    overflow: hidden;
    /*padding-bottom: 65px;*/
  }
  .details .item{
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    padding-right: 38px;
    margin-top: 24px;
  }
  .details .item h3{
    margin-left: 50px;
    width: 112px;
    margin-right: 33px;
    text-align: left;
    font-size: 16px;
    color: #666666;
  }
  .details .item p{
    font-size: 16px;
    color: #333333;
    width: 200px;
    text-align: left;
  }
  .details .item div{
    width: 626px;
    height: auto;
    overflow: hidden;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    flex-wrap:wrap;
  }
  .details .item div img{
    margin-right: 10px;
    border: 1px solid #f0f0f0;
  }
  .shadow{
    background-color: rgba(0,0,0,1);
    width: 100%;
    height: 2000px;
    position: fixed;
    display: flex;
    align-items: flex-start;
    justify-content: center;
  }
  .shadow img{
    margin-top: 200px;
    width: 30%;
    max-width: 400px;
    max-height: 700px;
    height: auto;
  }
</style>



